*{
  margin: 0;
  padding: 0;
}
.header{
  height: 80px;
  padding-left: 20px;
  line-height: 2.5;
  color: #fff;
  font-weight: 600;
  background-color: #05138a;
}
.list{
  height:100%;
  padding: 30px;
  border-bottom: 1px solid #000;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: start;
}
.list div{
  padding: 0;
  font-weight:600;
  display: flex;
}
.list>p{
  margin-top:  10px;
}
.list div p{
  margin-left: 5px;
}
.list h5{
  font-size: 15px;
}

.list h2{
  float: right;
  right: 50px;
  font-size:15px;
  color: #000;
  position: absolute;
}
button{margin: 10px 0 0 20px;
  width: 160px;
  height: 50px;

}
.modal{
  box-shadow:0 5px 5px 5px #d6d6d6;
  border-radius: 20px;
  background-color: #fff;
  top: 14%;
  left: 29%;
  position: absolute;
  padding: 30px;
  width: 300px;
  height: 300px;
}
.modal p:nth-child(2),
.modal p:nth-child(3){
  padding-top: 20px;
}